window.addEventListener('DOMContentLoaded', () => {

    'use strict';

    let images = require("./parts/images"),
        hamburger = require("./parts/hamburger"),
        pricesBtn = require("./parts/price-btn");
        



    images();
    hamburger(".hamburger", ".menu", ".menu__item");
    pricesBtn();
    
    new WOW().init();

    

    let eng = document.querySelectorAll(".btn-eng"),
        ru = document.querySelectorAll(".btn-ru"),
        engClass = document.querySelectorAll(".eng"),
        ruClass = document.querySelectorAll(".ru"),
        engClassInline = document.querySelectorAll(".eng-inline"),
        ruClassInline = document.querySelectorAll(".ru-inline");


    eng.forEach((item) => {
        item.addEventListener('click', () => {
            ruClass.forEach((item) => {
                item.style.display = "none";
            });
            ruClassInline.forEach((item) => {
                item.style.display = "none";
                
            });
            engClass.forEach((item) => {
                item.style.display = "block";
                item.style.letterSpacing = "1.4px";

            });
            engClassInline.forEach((item) => {
                item.style.display = "flex";
                item.style.letterSpacing = "1.4px";
            });
            

        });
    });

    ru.forEach((item) => {
        item.addEventListener('click', () => {
            ruClass.forEach((item) => {
                item.style.display = "block";
            });
            ruClassInline.forEach((item) => {
                item.style.display = "flex";
            });
            engClass.forEach((item) => {
                item.style.display = "none";
            });
            engClassInline.forEach((item) => {
                item.style.display = "none";
            });
        });
    });

    



});


$(document).ready(function(){

    $('.mobile-slide').slick({
        infinite: true,
        responsive: [
            {
              breakpoint: 800,
              settings: {
                arrows: true,
                prevArrow: '<button type="button" class="slick-prev arrows"><img src="icons/arrow-prev.png"></button>',
                nextArrow: '<button type="button" class="slick-next arrows"><img src="icons/arrow-next.png"></button>',
                centerPadding: '40px',
                slidesToShow: 3
              }
            },
            {
              breakpoint: 576,
              settings: {
                arrows: false,
                slidesToShow: 1,
                adaptiveHeight: true,
                // centerMode: true,
                dots: true
              }
            }
          ]
    });

    // light scroll
    $(".menu, .to-appointment, .footer__nav").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top}, 1500);
    });



    // send form
    $('form').submit(function(e) {
        e.preventDefault();
        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $(this).serialize()
        }).done(function() {
            $(this).find("input").val("");
            $('form, .appointment .title, .appointment__info, .appointment__require').fadeOut();
            $('body').css({'overflow' : 'hidden'});
            $(".overlay").fadeIn('slow');

            $('form').trigger('reset');
        });
        return false;
    });

    $('.close').on('click', function() {
        $(".overlay").fadeOut();
        $('body').css({'overflow' : ''});
        $('form, appointment title, appointment__info, .appointment__require').fadeIn();
    });




});
